.SignupPage {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 5rem 0;
  
    .container {
      width: 25rem;
      padding: 2rem 1rem;
      background-color: white;
      border-radius: 7px;
      box-shadow: 0 3px 3px rgba(0, 0, 0, 0.5);
    }
  
    &Title {
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 1.25rem;
      white-space: nowrap;
    }
  
    h1 {
      margin: 0 1rem;
    }
  
    .fa {
      color: white;
      font-size: 1.7rem;
    }
  
    h2 {
      color: white;
      font-size: 1.25rem;
      margin: 2rem 0;
    }
  
    span {
      margin: 2rem 0 1rem 0;
      color: white;
    }
  
    .Hyperlink {
      cursor: pointer;
      text-decoration: underline;
      font-weight: bold;
    }
  }
  